<template>
  <div>
    <v-row class="pa-0 ma-0 text-center" align="center" justify="center">
      <v-col class="pa-0" cols="12">
        <p class="p-title">방명록</p>
      </v-col>
    </v-row>

    <v-row class="pa-0 ma-0" align="center" justify="center">
      <v-col class="pa-0" cols="12">
        <v-row class="pa-0 ma-0">
          <v-col>
            <p class="p-subtitle">따뜻한 축하의 마음을 전해주세요.</p>
          </v-col>
        </v-row>
      </v-col>
    </v-row>

    <v-row class="pa-0 ma-0" align="center" justify="center">
      <v-col class="pa-0" cols="12">
        <v-row class="pa-0 ma-0" align="center" justify="center">
          <v-col cols="6" class="pa-0" style="height: 70px !important;">
            <Transition>
              <v-img 
                :src="require('@/assets/image/flying-hearts.gif')"
                height="70"
                class="mr-5"
                contain
                v-if="heartGroom"
              >
              </v-img>
            </Transition>
          </v-col>
          <v-col cols="6" class="pa-0">
            <Transition>
              <v-img 
                :src="require('@/assets/image/flying-hearts.gif')"
                height="70"
                class="mr-5"
                contain
                v-if="heartBride"
              >
            </v-img>
            </Transition>
          </v-col>
          <v-col cols="6" class="pa-0">
            <v-icon dense small color="red" @click="changeWriteTarget('groom')">
              mdi-cards-heart
            </v-icon>
            <span> x {{likeGroom}}</span>
          </v-col>
          <v-col cols="6" class="pa-0">
            <v-icon dense small color="red" @click="changeWriteTarget('bride')">
              mdi-cards-heart
            </v-icon>
            <span> x {{likeBride}}</span>
          </v-col>
          <v-col cols="6" class="pa-0">
            <v-avatar
              :size="isVertical ? 120 : 200"
              class="mb-4 cursor-pointer"
              :style="writeObject.target == 'groom' ? 'border: 3px solid rgba(84, 136, 199, 0.9)' : ''"
              @click="changeWriteTarget('groom')"
            >
              <img
                :src="require('@/assets/image/circle_dc.jpg')"
                alt="Dongchan"
              >
            </v-avatar>
            <p class="p-subtitle"><span class="span-groom">신랑</span>에게</p>
          </v-col>
          <v-col cols="6" class="pa-0">
            <v-avatar
              :size="isVertical ? 120 : 200"
              class="mb-4 cursor-pointer"
              :style="writeObject.target == 'bride' ? 'border: 3px solid rgba(195, 105, 169, 0.9)' : ''"
              @click="changeWriteTarget('bride')"
            >
              <img
                :src="require('@/assets/image/circle_hy.jpg')"
                alt="Hyeyoung"
              >
            </v-avatar>
            <p class="p-subtitle"><span class="span-bride">신부</span>에게</p>
          </v-col>
          <v-col cols="12" class="pa-0">
            <p class="p-subtitle4">※ 사진을 눌러 마음을 전할 사람을 선택해주세요</p>
          </v-col>
        </v-row>
        <v-row class="pa-0 ma-0" align="center" justify="center">
          <v-col class="pa-0" cols="10">
            <v-text-field
              v-model="writeObject.writer"
              clearable
              dense
              solo
              height="40"
              counter="30"
              class="mb-1"
              single-line
              :maxlength="30"
              placeholder="작성하시는 분의 성함을 입력해주세요."
              hint="성함"
            >
            </v-text-field>
          </v-col>
          <v-col class="pa-0 ma-0" cols="10">
            <v-text-field
              v-model="writeObject.contents"
              clearable
              dense
              solo
              height="40"
              counter="50"
              class="mb-1"
              :maxlength="50"
              placeholder="축하 메시지를 입력해주세요."
              hint="메시지"
            >
            </v-text-field>
          </v-col>
          <v-col class="pa-0 mb-n1" cols="10">
            <v-text-field
              v-model="writeObject.password"
              type="password"
              clearable
              dense
              solo
              height="40"
              counter="4"
              :maxlength="4"
              placeholder="비밀번호 숫자 4자리를 입력해주세요."
              hint="숫자 4자리 비밀번호"
            >
            </v-text-field>
          </v-col>
          <v-col class="pa-0" cols="3">
            <v-btn block color="white" dense rounded @click="addComment()">
              <p class="p-subtitle ma-0">작성</p>
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>

    <v-row class="pa-0 ma-0 mt-5" align="center" justify="center">
      <v-col cols="12">
        <v-img
          :src="require('@/assets/image/divider-small.png')"
          contain
          height="14"
          class="mb-5"
        >
        </v-img>
      </v-col>
      <v-col class="pa-0" cols="11" style="border: 1px solid #AAA;">
        <v-row 
          class="pa-0 ma-0 my-1" 
          align="center"
          v-for="(item, i) in guestbookData"
          :key="i"
          v-show="item.show"
        >
          <v-col class="pa-0" cols="2">
            <v-avatar
              size="40"
              class=""
              style="cursor: pointer;"
            >
              <v-img
                v-if="item.target == 'groom'"
                :src="require('@/assets/image/circle_dc.jpg')"
                alt="Dongchan"
                @click="heartUp('groom')"
              >
              </v-img>
              <v-img
                v-else
                :src="require('@/assets/image/circle_hy.jpg')"
                alt="Hyeyoung"
                @click="heartUp('bride')"
              >
              </v-img>
            </v-avatar>
          </v-col>
          <v-col class="pa-0" cols="8" style="text-align: left;">
            <p class="p-subtitle3 ma-0">{{item.writer}}</p>
            <p class="p-subtitle4 ma-0 ml-1">{{item.contents}}</p>
          </v-col>
          <v-col class="pa-0" cols="2">
            <p class="ma-0" style="font-size: 8px !important;">{{item.insert_dt}}</p>
            <v-btn
              icon
              dense
              x-small
              @click="toggleDeleteGuestbook(true, item)"
            >
              <v-icon
                color="red"
                small
              >
                mdi-window-close
              </v-icon>
            </v-btn>
          </v-col>
          <v-col class="pa-0" cols="12">
            <v-divider class="mt-1" v-show="(i + 1) < guestbookData.length"></v-divider>
          </v-col>
        </v-row>
        <v-row class="pa-0 ma-0 my-1">
          <v-col cols="12" v-show="!allDataShow">
            <v-row class="pa-0 ma-0" align="center" justify="center">
              <v-col cols="4" class="ma-0" style="border: 1px solid #BBB; background-color: rgba(0, 0, 0, 0); cursor: pointer;" @click="showMoreData()">
                <span style="font-size: .9rem;"><v-icon dense class="mr-3">mdi-arrow-down-drop-circle-outline</v-icon>방명록 더보기</span>
              </v-col>
              <v-col cols="1">
              </v-col>
              <v-col cols="4" class="ma-0" style="border: 1px solid #BBB; background-color: rgba(0, 0, 0, 0); cursor: pointer;" @click="showAllData()">
                <span style="font-size: .9rem;"><v-icon dense class="mr-3">mdi-arrow-down-drop-circle-outline</v-icon>모두 보기</span>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-col>
    </v-row>

    <v-snackbar
      v-model="alertBar"
      :timeout="alertTimeout"
      color="rgba(255, 247, 132, 0.9)"
      centered
      height="50"
    >
      <p class="p-subtitle2 ma-0 mb-n1 ml-2" style="color: black;">{{ alertText }}</p>
      <template v-slot:action="{ attrs }">
        <v-btn
          icon
          small
          v-bind="attrs"
          @click="alertBar = false"
        >
          <v-icon
            color="red"
            small
          >
            mdi-window-close
          </v-icon>
        </v-btn>
      </template>
    </v-snackbar>

    <DeleteGuestbook
      ref="DeleteGuestbook"
      :showPopup="showDeleteGuestbook"
      @hidePopup="toggleDeleteGuestbook(false, null)"
      @deleteComment="deleteComment"
    />
  </div>
</template>

<script>
import Axios from 'axios'
const https = require('https')
import DeleteGuestbook from '@/views/popup/DeleteGuestbook'

export default {
  writer: 'Guestbook',
  created() {

  },
  mounted() {
    this.getHeart()
    this.getComment()
  },
  beforeDestroy() {

  },
  props: {
    windowSize: {
      type: Object,
      required: false,
      default: {
        width: 0,
        height: 0,
      },
    },
    browserType: {
      type: String,
      required: false,
      default: "pc",
    },
    isVertical: {
      type: Boolean,
      required: false,
      default: true,
    },
  },
  components: {
    DeleteGuestbook
  },
  methods: {
    changeWriteTarget(target){
      this.writeObject.target = target
      // 좋아요 업데이트
      this.heartUp(target)
    },
    toggleDeleteGuestbook(flag, item) {
      this.showDeleteGuestbook = false
      this.showDeleteGuestbook = flag
      if (flag && item != null) {
        this.$refs.DeleteGuestbook.setItem(item)
      }
    },
    showAlert(alertText) {
      this.alertText = alertText
      this.alertBar = true
    },
    showMoreData() {
      var showCnt = 0
      for (var i=0; i<this.guestbookData.length; i++) {
        if (!this.guestbookData[i].show) {
          this.guestbookData[i].show = true
          showCnt++

          if (this.guestbookData.length == (i+1)) {
            this.allDataShow = true
            break
          }
          
          if (showCnt >= 10) {
            break
          }
        }
      }
    },
    showAllData() {
      for (var i=0; i<this.guestbookData.length; i++) {
        if (!this.guestbookData[i].show) {
          this.guestbookData[i].show = true
        }
      }

      this.allDataShow = true
    },
    getHeart() {
      var self = this

      var data = {
        
      }

      console.log("----------getHeart REQ----------")

      Axios({
        method: "post",
        url: self.serverUrl + "heart",
        data: data,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Headers": "*",
        },
        httpsAgent: new https.Agent({
          rejectUnauthorized: false,
        })
      }).then(function(response) {
        if (response !== null && response.status !== null && response.status == 200) {
          var resData = response.data

          if (resData !== null) {
            self.likeGroom = resData.groom
            self.likeBride = resData.bride
          }
        } else {
          console.log("response error")
          console.log(response)
        }
        
        console.log("----------getHeart RES----------")
      }).catch(function(error) {
        console.log(error)
      })
    },
    heartUp(target) {
      if (target == "groom") {
        this.heartGroom = true
      } else if (target == "bride") {
        this.heartBride = true
      }

      setTimeout(() => {
        this.heartGroom = false
        this.heartBride = false
      }, 2000)
      
      var self = this

      var data = {
        target: target,
      }

      console.log("----------heartUp REQ----------")

      Axios({
        method: "post",
        url: self.serverUrl + "heart/up",
        data: data,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Headers": "*",
        },
        httpsAgent: new https.Agent({
          rejectUnauthorized: false
        })
      }).then(function(response) {
        if (response !== null && response.status !== null && response.status == 200) {
          self.getHeart()
        } else {
          console.log("response error")
          console.log(response)
        }

        console.log("----------heartUp RES----------")
      }).catch(function(error) {
        console.log(error)
      })
    },
    addComment() {
      if (this.writeObject.target === null || this.writeObject.target == "") {
        this.showAlert("신랑 또는 신부를 선택해주세요")
        return false
      }

      if (this.writeObject.writer == "") {
        this.showAlert("작성하시는 분의 성함을 입력해주세요")
        return false
      }

      if (this.writeObject.contents == "") {
        this.showAlert("축하 메시지를 입력해주세요")
        return false
      }

      if (this.writeObject.password == "" || this.writeObject.password.length < 4) {
        this.showAlert("비밀번호 숫자 4자리를 입력해주세요")
        return false
      }

      var self = this

      var data = {
        target: this.writeObject.target, 
        writer: this.writeObject.writer,
        contents: this.writeObject.contents,
        password: this.writeObject.password,
      }

      console.log("----------addComment REQ----------")

      Axios({
        method: "post",
        url: self.serverUrl + "comment/add",
        data: data,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Headers": "*",
        },
        httpsAgent: new https.Agent({
          rejectUnauthorized: false
        })
      }).then(function(response) {
        if (response !== null && response.status !== null && response.status == 200) {
          self.showAlert("방명록을 작성했어요! 감사합니다♥")
          self.writeObject = {
            target: "",
            writer: "",
            contents: "",
            password: "",
          }
          self.getComment()
        } else {
          console.log("response error")
          console.log(response)
        }

        console.log("----------addComment RES----------")
      }).catch(function(error) {
        console.log(error)
      })
    },
    deleteComment(data) {
      if (data.password == "" || data.password.length < 4) {
        this.showAlert("비밀번호 숫자 4자리를 입력해주세요")
        return false
      }

      if (data.item.password != data.password) {
        this.showAlert("비밀번호가 올바르지 않습니다.")
        return false
      }

      var self = this

      var obj = {
        comment_id: data.item.comment_id,
        password: data.password
      }

      console.log("----------deleteComment REQ----------")
      this.showDeleteGuestbook = false

      Axios({
        method: "post",
        url: self.serverUrl + "comment/delete",
        data: obj,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Headers": "*",
        },
        httpsAgent: new https.Agent({
          rejectUnauthorized: false
        })
      }).then(function(response) {
        if (response !== null && response.status !== null && response.status == 200) {
          self.showAlert("방명록을 삭제했어요!")
          self.getComment()
        } else {
          console.log("response error")
          console.log(response)
        }

        console.log("----------deleteComment RES----------")
      }).catch(function(error) {
        console.log(error)
      })
    },
    getComment() {
      var self = this

      var data = {
        
      }

      console.log("----------getComment REQ----------")

      Axios({
        method: "post",
        url: self.serverUrl + "comment",
        data: data,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Headers": "*",
        },
        httpsAgent: new https.Agent({
          rejectUnauthorized: false
        })
      }).then(function(response) {
        if (response !== null && response.status !== null && response.status == 200) {
          var resData = response.data

          if (resData !== null) {
            self.guestbookData = resData

            for (var i=0; i<resData.length; i++) {
              var insert_dt = resData[i].insert_dt
              insert_dt = insert_dt.replace("T", " ").replace(".000+00:00", "")
              self.guestbookData[i].insert_dt = insert_dt

              if (i < 10) {
                self.guestbookData[i].show = true
                self.allDataShow = true
              } else {
                self.guestbookData[i].show = false
                self.allDataShow = false
              }
            }
          }
        } else {
          console.log("response error")
          console.log(response)
        }
        
        console.log("----------getComment RES----------")
      }).catch(function(error) {
        console.log(error)
      })
    },
  },
  data: () => ({
    //serverUrl: "https://ec2-54-180-122-239.ap-northeast-2.compute.amazonaws.com:9990/",
    serverUrl: "https://chan-page.ga:9990/",
    writeObject: {
      target: "",
      writer: "",
      contents: "",
      password: "",
    },
    liked: true,
    likeGroom: 0,
    likeBride: 0,
    heartGroom: false,
    heartBride: false,
    guestbookData: [
      // {target: 'groom', writer: '홍길동', contents: '결혼 축하해 잘살아라 행복하고 좋은일만 있길 바라!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!', insert_dt: '2022-11-26 12:30', password: '1111', show: true},
      // {target: 'bride', writer: '김아무개', contents: '축하해 !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!', insert_dt: '2022-11-26 12:30', show: true},
      // {target: 'groom', writer: '홍길동', contents: '결혼 축하해 잘살아라 행복하고 좋은일만 있길 바라!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!', insert_dt: '2022-11-26 12:30', show: true},
      // {target: 'bride', writer: '김아무개', contents: '축하해 !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!', insert_dt: '2022-11-26 12:30', show: true},
      // {target: 'groom', writer: '홍길동', contents: '결혼 축하해 잘살아라 행복하고 좋은일만 있길 바라!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!', insert_dt: '2022-11-26 12:30', show: true},
      // {target: 'bride', writer: '김아무개', contents: '축하해 !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!', insert_dt: '2022-11-26 12:30', show: true},
      // {target: 'groom', writer: '홍길동', contents: '결혼 축하해 잘살아라 행복하고 좋은일만 있길 바라!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!', insert_dt: '2022-11-26 12:30', show: true},
      // {target: 'bride', writer: '김아무개', contents: '축하해 !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!', insert_dt: '2022-11-26 12:30', show: true},
      // {target: 'groom', writer: '홍길동', contents: '결혼 축하해 잘살아라 행복하고 좋은일만 있길 바라!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!', insert_dt: '2022-11-26 12:30', show: true},
      // {target: 'bride', writer: '김아무개', contents: '축하해 !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!', insert_dt: '2022-11-26 12:30', show: false},
      // {target: 'groom', writer: '홍길동', contents: '결혼 축하해 잘살아라 행복하고 좋은일만 있길 바라!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!', insert_dt: '2022-11-26 12:30', show: false},
      // {target: 'bride', writer: '김아무개', contents: '축하해 !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!', insert_dt: '2022-11-26 12:30', show: false},
    ],
    allDataShow: false,
    showDeleteGuestbook: false,
    alertBar: false,
    alertText: '',
    alertTimeout: 2000,
  }),
}
</script>

<style scoped>
  .v-enter-active,
  .v-leave-active {
    transition: opacity 0.5s ease;
  }

  .v-enter-from,
  .v-leave-to {
    opacity: 0;
  }
</style>