<template>
  <div>
    <v-row class="pa-0 ma-0 text-center" align="center" justify="center">
      <v-col class="pa-0">
        <p class="p-title" style="">예식 안내</p>
      </v-col>
    </v-row>

    <v-row class="pa-0 ma-0">
      <v-col>
        <p class="mb-9 p-subtitle" style="">예식, 식사, 주차 정보를 안내드립니다.</p>
      </v-col>
    </v-row>

    <v-row class="pa-0 ma-0" align="center" justify="center">
      <v-col class="pt-4" cols="12" style="background-color: rgba(0, 0, 0, 0.1);">
        <p class="p-title ma-0">2022. 11. 26. Sat. 12:30 PM<br/>라온제나 강남 8층 플로렌스홀</p>
      </v-col>
    </v-row>

    <v-row class="pa-0 ma-0" align="center" justify="center">
      <v-col class="pa-0" cols="12">
        <v-tabs
          v-model="tab"
          background-color="transparent"
          class="basil pa-0 ma-0"
          centered
          fixed-tabs
          icons-and-text
        >
          <v-tabs-slider class="basil--text"></v-tabs-slider>

          <v-tab href="#map" class="basil--text">
            <span>장소 안내</span>
            <v-icon>mdi-map-marker</v-icon>
          </v-tab>
          <v-tab href="#food" class="basil--text">
            <span>식사 안내</span>
            <v-icon>mdi-silverware-fork-knife</v-icon>
          </v-tab>
          <v-tab href="#parking" class="basil--text">
            <span>주차 안내</span>
            <v-icon>mdi-parking</v-icon>
          </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab" style="" touchless>
          <v-tab-item
            value="map"
          >
            <v-card flat>
              <Map 
                ref="Map"
                :windowSize.sync="windowSize"
                :browserType.sync="browserType"
                :isVertical.sync="isVertical"
              />
            </v-card>
          </v-tab-item>
          <v-tab-item
            value="food"
          >
            <v-card flat>
              <Food 
                ref="Food"
                :windowSize.sync="windowSize"
                :browserType.sync="browserType"
                :isVertical.sync="isVertical"
              />
            </v-card>
          </v-tab-item>
          <v-tab-item
            value="parking"
          >
            <v-card flat>
              <Parking 
                ref="Parking"
                :windowSize.sync="windowSize"
                :browserType.sync="browserType"
                :isVertical.sync="isVertical"
              />
            </v-card>
          </v-tab-item>
        </v-tabs-items>
        <v-row class="pa-0 ma-0" align="center" justify="center">
          <!-- <v-col>
            <v-btn @click="openMiniMap()" class="" height="40" rounded>
              <p class="p-subtitle2 mb-n1">약도 이미지</p>
            </v-btn>
          </v-col> -->
          <v-col class="px-0">
            <v-btn @click="openNaverMapApp()" class="pa-3 mr-2" height="40" rounded color="white">
              <v-img
                :src="require('@/assets/image/button/navermap.png')"
                height="20"
                width="20"
                class="rounded mr-2"
                style="border: 1px solid #EEE;"
              >
              </v-img>
              <p class="p-subtitle3 mb-n1">네이버지도</p>
            </v-btn>
            <v-btn @click="openKakaoMapApp()" class="pa-3 mr-2" height="40" rounded color="white">
              <v-img
                :src="require('@/assets/image/button/kakaomap.png')"
                height="20"
                width="20"
                class="rounded mr-2"
                style="border: 1px solid #EEE;"
              >
              </v-img>
              <p class="p-subtitle3 mb-n1">카카오맵</p>
            </v-btn>
            <v-btn @click="openTmapApp()" class="pa-3" height="40" rounded color="white">
              <v-img
                :src="require('@/assets/image/button/tmap.png')"
                height="20"
                width="20"
                class="rounded mr-2"
                style="border: 1px solid #EEE;"
              >
              </v-img>
              <p class="p-subtitle3 mb-n1">티맵</p>
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
    <v-row class="pa-0 ma-0" align="center" justify="center">
      <v-col cols="12">
        <v-img
          :src="require('@/assets/image/divider-small.png')"
          contain
          height="14"
          class="mt-7"
        >
        </v-img>
      </v-col>
      <v-col class="pa-0" cols="12">
        <p class="p-subtitle mt-9">예식장 내 엘리베이터가 협소하여<br/>불편을 겪으실 수 있으니</p>
        <p class="p-subtitle">너그러운 마음으로 여유롭게 참석하시어<br/>예식의 순간을 온전히 지켜봐주시기 바랍니다.</p>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import Map from "@/views/components/Map"
import Food from "@/views/components/Food"
import Parking from "@/views/components/Parking"

export default {
  name: 'Info',
  created() {

  },
  mounted() {
    setTimeout(() => {this.initMap()}, 500)
  },
  beforeDestroy() {

  },
  props: {
    windowSize: {
      type: Object,
      required: false,
      default: {
        width: 0,
        height: 0,
      },
    },
    browserType: {
      type: String,
      required: false,
      default: "pc",
    },
    isVertical: {
      type: Boolean,
      required: false,
      default: true,
    },
  },
  components: {
    Map,
    Food,
    Parking
  },
  methods: {
    initMap() {
      if (!this.isLoaded) {
        this.$refs.Map.initNaverMap()
        this.isLoaded = true
      }
    },
    openMiniMap() {
      console.log("openMiniMap")
    },
    openTmapApp() {
      console.log("openTmapApp")
      window.location.href = "https://apis.openapi.sk.com/tmap/app/routes?appKey=l7xxf3df8e921a6e43beb8fbcb43854b1c20&name=라온제나 강남&lon=127.038589254902&lat=37.5169632798641"
    },
    openNaverMapApp() {
      console.log("openNaverMapApp")
      if (this.browserType == "android") {
        window.location.href = "intent://route/public?dlat=37.5169632798641&dlng=127.038589254902&dname=%EB%9D%BC%EC%98%A8%EC%A0%9C%EB%82%98%20%EA%B0%95%EB%82%A8&appname=ldc0728.github.io/invitation-publish#Intent;scheme=nmap;action=android.intent.action.VIEW;category=android.intent.category.BROWSABLE;package=com.nhn.android.nmap;end"
      } else if (this.browserType == "iphone") {
        var clickedAt = +new Date()
        window.location.href = "nmap://route/public?dlat=37.5169632798641&dlng=127.038589254902&dname=%EB%9D%BC%EC%98%A8%EC%A0%9C%EB%82%98%20%EA%B0%95%EB%82%A8&appname=ldc0728.github.io/invitation-publish"

        setTimeout(function() {
          if (+new Date() - clickedAt < 2000) {
            location.href = 'http://itunes.apple.com/app/id311867728?mt=8'
          }
        }, 1500)
      } else {
        window.location.href = "https://naver.me/FXZrWN73"
      }
    },
    openKakaoMapApp() {
      console.log("openKakaoMapApp")
      window.location.href = "kakaomap://search?q=라온제나 강남&p=37.5169632798641,127.038589254902"
    },
  },
  data: () => ({
    isLoaded: false,
    tab: null,
  }),
}
</script>

