<template>
  <v-app>
    <Header
      @setPage="setPage"
      @showPopupAccountNumber="showPopupAccountNumber"
      :currentPage.sync="currentPage"
    >
    </Header>

    <v-main>
      <router-view
        ref="Main"
        :currentPage.sync="currentPage"
      />
    </v-main>

  </v-app>
</template>

<script>
import Header from '@/views/components/Header'

export default {
  name: 'App',
  created() {},
  mounted() {},
  beforeDestroy() {},
  components: {
    Header
  },
  methods: {
    setPage(page) {
      //this.currentPage = page
      this.$refs.Main.scrollToPage(page)
    },
    showPopupAccountNumber(target) {
      this.$refs.Main.showPopupAccountNumber(target)
    }
  },
  watch: {

  },
  data: () => ({
    currentPage: "Intro",
  }),
}
</script>