<!--포토 갤러리-->
<template>
  <div class="pa-0 ma-0" style="">
    <v-row class="pa-0 ma-0 text-center" align="center" justify="center">
      <v-col class="pa-0">
        <p class="p-title" style="">사진첩</p>
      </v-col>
    </v-row>

    <v-row class="pa-0 ma-0">
      <v-col>
        <p class="mb-4 p-subtitle" style="">저희의 사랑을 담은 순간</p>
      </v-col>
    </v-row>

    <v-row class="pa-0 ma-0 text-center" align="center" justify="center">
      <!-- 메인 포토 -->
      <!-- <v-col cols="12" class="">
        <v-carousel 
          hide-delimiters
          show-arrows-on-hover
          :height="photoHeightHor"
          v-model="selected"
          cycle
          interval="10000"
        >
          <v-carousel-item
            v-for="(item, i) in photoItemsHor"
            :key="i"
            :src="item.src"
          ></v-carousel-item>
        </v-carousel>
      </v-col> -->
      
      <!-- 미리보기 -->
      <v-col cols="12" class="pt-2">
        <v-container class="pa-0">
          <v-item-group
            v-model="selected"
          >
            <v-row 
              class="pa-0 ma-0"
            >
              <v-col
                class="pa-1"
                v-for="(item, idx) in photoItems"
                :key="idx"
                cols="3"
                md="3"
                v-show="item.show"
              >
                <v-hover v-slot="{ hover }">
                  <v-item v-slot="{ active }" :class="{ 'on-hover': hover }">
                    <v-img
                      :src="item.thumbnail"
                      :lazy-src="item.thumbnail"
                      class="pa-2"
                      :style="active ? 'border: 2px solid #CCC; opacity: 1 !important;' : ''"
                      @click="togglePhotoViewer(true, idx)"
                    >
                      <template v-slot:placeholder>
                        <v-row
                          class="fill-height ma-0"
                          align="center"
                          justify="center"
                        >
                          <v-progress-circular
                            indeterminate
                            color="grey lighten-5"
                          ></v-progress-circular>
                        </v-row>
                      </template>
                    </v-img>
                  </v-item>
                </v-hover>
              </v-col>
            </v-row>
            <v-col cols="12" v-show="!allPhotoShow">
              <v-row class="pa-0 ma-0" align="center" justify="center">
                <v-col cols="6" style="border: 1px solid #BBB; background-color: rgba(0, 0, 0, 0); cursor: pointer;" @click="showMorePhoto()">
                  <span style="font-size: .9rem;"><v-icon dense class="mr-3">mdi-arrow-down-drop-circle-outline</v-icon>사진 더보기</span>
                </v-col>
              </v-row>
            </v-col>
          </v-item-group>
        </v-container>
      </v-col>
    </v-row>

    <PhotoViewer
      ref="PhotoViewer"
      :showPopup="showPhotoViewer"
      :photoItems="photoItems"
      :isVertical.sync="isVertical"
      @hidePopup="togglePhotoViewer(false, null)"
    />
  </div>
</template>

<script>
import PhotoViewer from "@/views/popup/PhotoViewer"

export default {
  name: 'Gallery',
  created() {

  },
  mounted() {
    this.setPhotoHeight()
    if (this.photoItems.length < 9) {
      this.allPhotoShow = true
    }
  },
  beforeDestroy() {

  },
  props: {
    windowSize: {
      type: Object,
      required: false,
      default: {
        width: 0,
        height: 0,
      },
    },
    browserType: {
      type: String,
      required: false,
      default: "pc",
    },
    isVertical: {
      type: Boolean,
      required: false,
      default: true,
    },
  },
  watch: {

  },
  components: {
    PhotoViewer,
  },
  methods: {
    setPhotoHeight() {
      this.photoHeightHor = this.windowWidth - 100
      this.photoHeightVer = this.windowWidth + 200
      this.photoHeightSqu = this.windowWidth
    },
    showMorePhoto() {
      this.showPhotoViewer = false
      var showCnt = 0
      for (var i=0; i<this.photoItems.length; i++) {
        if (!this.photoItems[i].show) {
          this.photoItems[i].show = true
          showCnt++

          if (this.photoItems.length == (i+1)) {
            this.allPhotoShow = true
            break
          }

          if (showCnt >= 8) {
            break
          }
        }
      }
    },
    togglePhotoViewer(flag, target) {
      this.showPhotoViewer = false
      this.showPhotoViewer = flag
      if (flag && target != null) {
        this.$refs.PhotoViewer.setPhotoData(target)
      }
    }
  },
  data: () => ({
    photoHeightHor: 300,
    photoHeightVer: 300,
    photoHeightSqu: 300,
    allPhotoShow: false,
    photoItems: [
      {
        show: true,
        vertical: false,
        thumbnail: require('@/assets/image/wedding/horizontal/1.jpg'),
        src: require('@/assets/image/wedding/horizontal/1_thumbnail.jpg')
      },
      // {
      //   show: true,
      //   vertical: false,
      //   thumbnail: require('@/assets/image/wedding/horizontal/2.jpg'),
      //   src: require('@/assets/image/wedding/horizontal/2.jpg')
      // },
      // {
      //   show: true,
      //   vertical: false,
      //   thumbnail: require('@/assets/image/wedding/horizontal/3.jpg'),
      //   src: require('@/assets/image/wedding/horizontal/3.jpg')
      // },
      // {
      //   show: true,
      //   vertical: false,
      //   thumbnail: require('@/assets/image/wedding/horizontal/4.jpg'),
      //   src: require('@/assets/image/wedding/horizontal/4.jpg')
      // },
      // {
      //   show: true,
      //   vertical: false,
      //   thumbnail: require('@/assets/image/wedding/horizontal/5.jpg'),
      //   src: require('@/assets/image/wedding/horizontal/5.jpg')
      // },
      // {
      //   show: true,
      //   vertical: false,
      //   thumbnail: require('@/assets/image/wedding/horizontal/6.jpg'),
      //   src: require('@/assets/image/wedding/horizontal/6.jpg')
      // },
      // {
      //   show: false,
      //   vertical: false,
      //   thumbnail: require('@/assets/image/wedding/horizontal/7.jpg'),
      //   src: require('@/assets/image/wedding/horizontal/7.jpg')
      // },
      // {
      //   show: false,
      //   vertical: false,
      //   thumbnail: require('@/assets/image/wedding/horizontal/8.jpg'),
      //   src: require('@/assets/image/wedding/horizontal/8.jpg')
      // },
      // {
      //   show: false,
      //   vertical: false,
      //   thumbnail: require('@/assets/image/wedding/horizontal/9.jpg'),
      //   src: require('@/assets/image/wedding/horizontal/9.jpg')
      // },
    ],
    selected: [],
    showPhotoViewer: false,
  }),
}
</script>

<style scoped>
  .v-image {
    transition: opacity .4s ease-in-out;
  }
  .v-image:not(.on-hover) {
    opacity: 0.6;
  }
</style>