<template>
  <v-app-bar
    app
    bottom
    color="#FFF"
    height="60"
    style="opacity: 0.9;"
  >
    <v-row align="center" justify="center" class="text-center pa-0 ma-0">
      <v-col cols="12" style="font-size: .6em; font-weight: 600;">
        <v-row class="pa-0 ma-0" align="center" justify="center">
          <v-col cols="2" class="pa-0 ma-0">
            <p class="ma-0 mr-1">today | {{todayCnt}}</p>
            <p class="ma-0 mr-1">all | {{allCnt}}</p>
          </v-col>
          <v-col cols="2" class="pa-0 ma-0 cursor-pointer">
            <span class="" @click="onSetPage('Info')"><v-icon dense small>mdi-map-marker</v-icon>안내</span>
          </v-col>
          <v-col cols="3" class="pa-0 ma-0 cursor-pointer">
            <span class="" @click="onSetPage('Gallery')"><v-icon dense small>mdi-camera</v-icon> 사진첩</span>
          </v-col>
          <v-col cols="2" class="pa-0 ma-0 cursor-pointer">
            <span class="" @click="onSetPage('Guestbook')"><v-icon dense small>mdi-lead-pencil</v-icon> 방명록</span>
          </v-col>
          <v-col cols="1" class="pa-0 ma-0 cursor-pointer" @click="onSetPage('MainPhoto')">
            <v-icon dense small class="ml-3">mdi-chevron-double-up</v-icon>
          </v-col>
          <v-col cols="1" class="pa-0 ma-0 cursor-pointer" @click="onSetPage('ThanksTo')">
            <v-icon dense small class="ml-6">mdi-chevron-double-down</v-icon>
          </v-col>
          <v-col cols="1" class="pa-0 ma-0">
            <v-speed-dial
              v-model="fab"
              :top="false"
              :bottom="true"
              :right="true"
              :left="false"
              :direction="'top'"
              :open-on-hover="false"
              :transition="'slide-y-reverse-transition'"
              class="mb-9"
            >
              <template v-slot:activator>
                <v-btn
                  v-model="fab"
                  color="red"
                  dark
                  fab
                  small
                >
                  <v-icon dense v-if="fab">
                    mdi-close
                  </v-icon>
                  <v-icon dense v-else>
                    mdi-cards-heart
                  </v-icon>
                </v-btn>
              </template>
              <v-btn
                fab
                small
                color="white"
                @click="showPopupAccountNumber('groom')"
              >
                <v-avatar
                  size="40"
                  class=""
                  style="cursor: pointer;"
                >
                  <img
                    :src="require('@/assets/image/circle_dc.jpg')"
                    alt="Dongchan"
                  >
                </v-avatar>
              </v-btn>
              <v-btn
                fab
                small
                color="white"
                @click="showPopupAccountNumber('bride')"
              >
                <v-avatar
                  size="40"
                  class=""
                  style="cursor: pointer;"
                >
                  <img
                    :src="require('@/assets/image/circle_hy.jpg')"
                    alt="Hyeyoung"
                  >
                </v-avatar>
              </v-btn>
              <v-btn
                fab
                small
                color="white"
                @click="openNaverMapApp()"
              >
                <v-avatar
                  size="40"
                  class=""
                  style="cursor: pointer;"
                >
                  <img
                    :src="require('@/assets/image/button/navermap.png')"
                  >
                </v-avatar>
              </v-btn>
              <v-btn
                fab
                small
                color="white"
                @click="openKakaoMapApp()"
              >
                <v-avatar
                  size="40"
                  class=""
                  style="cursor: pointer;"
                >
                  <img
                    :src="require('@/assets/image/button/kakaomap.png')"
                  >
                </v-avatar>
              </v-btn>
              <v-btn
                fab
                small
                color="white"
                @click="openTmapApp()"
              >
                <v-avatar
                  size="40"
                  class=""
                  style="cursor: pointer;"
                >
                  <img
                    :src="require('@/assets/image/button/tmap.png')"
                  >
                </v-avatar>
              </v-btn>
            </v-speed-dial>
          </v-col>
        </v-row>
        <!-- <span class="mr-9" @click="onSetPage('Gallery')" :style="currentPage == 'Gallery' ? 'color: #76F;' : ''"><v-icon dense>mdi-camera</v-icon> 사진첩</span>
        <span class="mr-9" @click="onSetPage('About')" :style="currentPage == 'About' ? 'color: #76F;' : ''"><v-icon dense>mdi-map-marker</v-icon> 안내</span>
        <span class="mr-9" @click="onSetPage('ContactUs')" :style="currentPage == 'ContactUs' ? 'color: #76F;' : ''">
          <v-img
            :src="require('@/assets/image/button/KakaoTalk_logo.png')"
            max-height="32"
            max-width="32"
            class="pa-0 ma-0"
            style="display: inline-block;"
          >
          </v-img>
        </span> -->
      </v-col>
    </v-row>
  </v-app-bar>
</template>

<script>
import Axios from 'axios'
// import Logo from '@/views/components/Logo.vue'

export default {
  name: 'Header',
  created() {
    var browserInfo = navigator.userAgent.toLowerCase()
    var browser = "pc"
    
    if (browserInfo.indexOf("android") !== -1) {
      browser = "android"
    } else if (browserInfo.indexOf("iphone") !== -1) {
      browser = "iphone"
    } else if (browserInfo.indexOf("macintosh") !== -1) {
      browser = "mac"
    }
    
    this.browserType = browser
  },
  mounted() {
    //this.get()
  },
  beforeDestroy() {},
  props: {
    currentPage: {
      type: String,
      required: true,
      default: "Intro",
    },
  },
  components: {
    // Logo
  },
  methods: {
    onSetPage(page) {
      this.$emit("setPage", page)
    },
    openTmapApp() {
      console.log("openTmapApp")
      window.location.href = "https://apis.openapi.sk.com/tmap/app/routes?appKey=l7xxf3df8e921a6e43beb8fbcb43854b1c20&name=라온제나 강남&lon=127.038589254902&lat=37.5169632798641"
    },
    openNaverMapApp() {
      console.log("openNaverMapApp")
      if (this.browserType == "android") {
        window.location.href = "intent://route/public?dlat=37.5169632798641&dlng=127.038589254902&dname=%EB%9D%BC%EC%98%A8%EC%A0%9C%EB%82%98%20%EA%B0%95%EB%82%A8&appname=ldc0728.github.io/invitation-publish#Intent;scheme=nmap;action=android.intent.action.VIEW;category=android.intent.category.BROWSABLE;package=com.nhn.android.nmap;end"
      } else if (this.browserType == "iphone") {
        var clickedAt = +new Date()
        window.location.href = "nmap://route/public?dlat=37.5169632798641&dlng=127.038589254902&dname=%EB%9D%BC%EC%98%A8%EC%A0%9C%EB%82%98%20%EA%B0%95%EB%82%A8&appname=ldc0728.github.io/invitation-publish"

        setTimeout(function() {
          if (+new Date() - clickedAt < 2000) {
            location.href = 'http://itunes.apple.com/app/id311867728?mt=8'
          }
        }, 1500)
      } else {
        window.location.href = "https://naver.me/FXZrWN73"
      }
    },
    openKakaoMapApp() {
      console.log("openKakaoMapApp")
      window.location.href = "kakaomap://search?q=라온제나 강남&p=37.5169632798641,127.038589254902"
    },
    showPopupAccountNumber(target) {
      this.$emit("showPopupAccountNumber", target)
    },
    getHeart() {
      var self = this

      var data = {
        
      }

      console.log("----------getHeart REQ----------")

      Axios({
        method: "post",
        url: self.serverUrl + "heart",
        data: data,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Headers": "*",
        },
      }).then(function(response) {
        if (response !== null && response.status !== null && response.status == 200) {
          var resData = response.data

          if (resData !== null) {
            self.todayCnt = resData.today
            self.allCnt = resData.all
          }
        } else {
          console.log("response error")
          console.log(response)
        }
        
        console.log("----------getHeart RES----------")
      }).catch(function(error) {
        console.log(error)
      })
    },
  },
  data: () => ({
    fab: false,
    browserType: "pc",
    todayCnt: 99,
    allCnt: 9999,
  }),
}
</script>

<style scoped>
  .cursor-pointer {
    cursor: pointer;
  }
</style>