<template>
  <v-container class="pa-0" :style="'display: block; max-width: ' + mainMaxWidth">
    <v-row class="text-center" justify="center" align="center">
      <transition name="slide-fade">
        <v-col class="pa-0 ma-0" cols="12" v-show="currentPage == 'Intro'">
          <Intro
            ref="Intro"
            :windowHeight.sync="windowHeight"
            :windowWidth.sync="windowWidth"
          >
          </Intro>
        </v-col>
      </transition>
      <!-- <transition name="slide-fade">
        <v-col cols="12" class="" v-show="currentPage == 'Gallery'">
          <Gallery
            :windowHeight.sync="windowHeight"
            :windowWidth.sync="windowWidth"
          >
          </Gallery>
        </v-col>
      </transition>
      <transition name="slide-fade">
        <v-col cols="12" class="" v-show="currentPage == 'About'">
          <About
            ref="About"
            :windowHeight.sync="windowHeight"
            :windowWidth.sync="windowWidth"
          >
          </About>
        </v-col>
      </transition>
      <transition name="slide-fade">
        <v-col cols="12" class="" v-show="currentPage == 'ContactUs'">
          <ContactUs
            :windowHeight.sync="windowHeight"
            :windowWidth.sync="windowWidth"
          >
          </ContactUs>
        </v-col>
      </transition> -->
    </v-row>
  </v-container>
</template>

<script>
import Intro from './module/Intro.vue'
// import Gallery from './module/Gallery.vue'
// import About from './module/About.vue'
// import ContactUs from './module/ContactUs.vue'

export default {
  name: 'Main',
  created() {
    var browserInfo = navigator.userAgent.toLowerCase()
    var browser = "pc"
    
    if (browserInfo.indexOf("android") !== -1) {
      browser = "android"
      this.mainMaxWidth = "85% !important;"
    } else if (browserInfo.indexOf("iphone") !== -1) {
      browser = "iphone"
      this.mainMaxWidth = "86% !important;"
    } else if (browserInfo.indexOf("macintosh") !== -1) {
      browser = "mac"
      this.mainMaxWidth = "95% !important;"
    } else {
      this.mainMaxWidth = "100% !important;"
    }
    
    this.browserType = browser
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize)
    })
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.onResize)
  },
  props: {
    currentPage: {
      type: String,
      required: true,
      default: "Intro",
    },
  },
  components: {
    Intro,
    // Gallery,
    // About,
    // ContactUs
  },
  methods: {
    onResize() {
      this.windowHeight = window.innerHeight
      this.windowWidth = window.innerWidth
    },
    scrollToPage(page) {
      this.$refs.Intro.scrollToPage(page)
    },
    showPopupAccountNumber(target) {
      this.$refs.Intro.showPopupAccountNumber(target)
    }
  },
  watch: {
    
  },
  data: () => ({
    browserType: "pc",
    windowHeight: window.innerHeight,
    windowWidth: window.innerWidth,
    mainMaxWidth: "85%",
  }),
}
</script>

<style scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity .6s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
/* 애니메이션 진입 및 진출은 다른 지속 시간 및  */
/* 타이밍 기능을 사용할 수 있습니다. */
.slide-fade-enter-active, .slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>