<script>
const position = new naver.maps.LatLng(37.5169632798641, 127.038589254902)

export default {
  name: 'Map',
  created() {

  },
  mounted() {
    
  },
  beforeDestroy() {

  },
  props: {
    windowSize: {
      type: Object,
      required: false,
      default: {
        width: 0,
        height: 0,
      },
    },
    browserType: {
      type: String,
      required: false,
      default: "pc",
    },
    isVertical: {
      type: Boolean,
      required: false,
      default: true,
    },
  },
  components: {
    
  },
  computed: {
    
  },
  methods: {
    initNaverMap() {
      console.log("initNaverMap")
      this.mapWidth = window.innerWidth - 40

      var mapOptions = {
        center: position.destinationPoint(0, 200),
        draggable: true,
        zoom: 16
      }

      var map = new naver.maps.Map('naver-map', mapOptions)

      //map.setSize(new naver.maps.Size(this.mapWidth, 450))

      var marker = new naver.maps.Marker({
        position: position,
        map: map,
        title: '라온제나 강남',
        animation: naver.maps.Animation.DROP
      })

      var infoContent = [
        '<div class="info-window">',
        '  <img src="' + require('@/assets/image/laonzena2.png') + '" width="210" height="150" alt="라온제나 강남" class="thumb" /><br />',
        '  <p class="p-title" style="margin-bottom: 5px;">라온제나 강남 플로렌스홀',
        '  <p style="font-size: 11px; margin-bottom: 5px;">서울 강남구 학동로47길 5 (논현동) | 8층</p>',
        '  <p style="font-size: 11px; margin-bottom: 0px;">안내 : 02-3447-1133</p>',
        '</div>'
      ].join('')

      var infoWindow = new naver.maps.InfoWindow({
        content: infoContent,
        borderColor: "#2dbbfa",
        anchorSize: new naver.maps.Size(15, 15),
      })

      naver.maps.Event.addListener(marker, "click", function(e) {
        if (infoWindow.getMap()) {
          infoWindow.close()
        } else {
          infoWindow.open(map, marker)
        }
      })

      this.map = map
      this.marker = marker
      this.infoWindow = infoWindow

      infoWindow.open(map, marker)
    },
  },
  data: () => ({
    map: null,
    infoWindow: null,
    marker: null,
    mapWidth: 0,
  }),
}
</script>

