<template>
  <div class="pa-0 ma-0">
    <v-row class="pa-0 ma-0" align="center" justify="center">
      <v-col cols="12" class="pa-0 ma-0">
        <p class="p-title">라온제나 강남 8층 플로렌스홀</p>
        <p class="p-subtitle">2022년 11월 26일 토요일</p>
        <p class="p-subtitle">오후 12시 30분 예식이 시작됩니다.</p>
      </v-col>
    </v-row>
    
    <v-row class="pa-0 ma-0" align="center" justify="center">
      <v-col cols="12">
        <v-img
          :src="require('@/assets/image/dday.png')"
          contain
          class="ma-0"
        >
        </v-img>
        <!-- <v-sheet height="400" width="600" style="display: inline-block;">
          <v-calendar
            ref="calendar"
            v-model="weddingDay"
            :now="weddingDay"
            :value="weddingDay"
            color="#00AAFF"
            :events="events"
            :event-color="getEventColor"
            :type="'month'"
            @click:event="showEvent"
            @click:more="viewDay"
            @click:date="viewDay"
            @change="updateRange"
          ></v-calendar>
        </v-sheet> -->
      </v-col>
    </v-row>
    <v-row class="pa-0 ma-0">
      <v-col cols="12" class="pa-0 mb-4">
        <v-img
          :src="require('@/assets/image/divider-small.png')"
          contain
          height="14"
          class="mt-3"
        >
        </v-img>
      </v-col>
      <v-col cols="12">
        <p class="p-title">결혼식까지 남은 시간</p>
        <p class="p-title">{{dday}}</p>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  name: 'DDay',
  created() {},
  mounted() {
    this.getDDay()
  },
  beforeDestroy() {},
  methods: {
    getDDay() {
      var today = new Date()
      var weddingDay = new Date(this.weddingDay)
      var def = weddingDay.getTime() - today.getTime()
      var dday = Math.ceil(def / (1000 * 60 * 60 * 24))

      if (dday == 0) {
        this.dday = 'Today'
      } else if (dday < 0) {
        this.dday = dday + '일'
      } else {
        this.dday = '+ ' + dday + '일'
      }
    },
  },
  data: () => ({
    weddingDay: '2022-11-26 12:30',
    dday: 1,
  }),
}
</script>

