<script>
import MainPhoto from '@/views/components/intro/MainPhoto'
import Greeting from '@/views/components/intro/Greeting'
import Gallery from '@/views/components/intro/Gallery'
import DDay from '@/views/components/intro/DDay'
import Info from '@/views/components/intro/Info'
import Comment from '@/views/components/intro/Comment'
import Guestbook from '@/views/components/intro/Guestbook'

export default {
  name: 'Intro',
  created() {
    window.addEventListener('resize', this.handleResize)
    this.handleResize()
  },
  mounted() {
    var browserInfo = navigator.userAgent.toLowerCase()
    var browser = "pc"
    this.windowSize.width = window.innerWidth
    
    if (browserInfo.indexOf("android") !== -1) {
      browser = "android"
    } else if (browserInfo.indexOf("iphone") !== -1) {
      browser = "iphone"
    } else if (browserInfo.indexOf("macintosh") !== -1) {
      browser = "mac"
    } else {
      browser = "pc"
    }

    var diff = (window.innerWidth - window.innerHeight)
    if (diff > 0) {
      this.windowSize.height = window.innerWidth + ((diff * 2) - 130)
      if (browser == "pc") {
        this.windowSize.height -= 600
      }
    } else {
      this.windowSize.height = window.innerWidth + ((diff / 2) + 330)
    }
    
    this.browserType = browser

    if (this.windowSize.width > this.windowSize.height) {
      this.isVertical = false
    }

    var date = new Date()
    date.setHours(date.getHours + 9)
    //this.test = date.toISOString().split('.')[0].replace(/[^\d]/gi,'')
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.handleResize)
  },
  props: {
    windowHeight: {
      type: Number,
      required: false,
      default: 0,
    },
    windowWidth: {
      type: Number,
      required: false,
      default: 0,
    },
  },
  watch: {
    
  },
  components: {
    MainPhoto,
    Greeting,
    Gallery,
    DDay,
    Info,
    Comment,
    Guestbook
  },
  methods: {
    scrollToPage(page) {
      const el = this.$el.getElementsByClassName(page)[0];
      
      if (el) {
        el.scrollIntoView({behavior: 'smooth'});
      }
    },
    handleResize() {
      if (window.innerWidth > window.innerHeight) {
        this.isVertical = false
      } else {
        this.isVertical = true
      }

      if (this.browserType == "pc") {
        var diff = (window.innerWidth - window.innerHeight)
        if (diff > 0) {
          this.windowSize.height = window.innerWidth + ((diff * 2) - 130)
          this.windowSize.height -= 600
        } else {
          this.windowSize.height = window.innerWidth + ((diff / 2) + 330)
        }
      }
    },
    showPopupAccountNumber(target) {
      this.$refs.Comment.toggleAccountNumber(true, target)
    }
  },
  data: () => ({
    isVertical: true,
    browserType: "pc",
    windowSize: {
      width: 0,
      height: 0,
    },
  }),
}
</script>

