<template>
  <v-dialog v-model="showPopup" persistent width="500">
    <v-card style="text-align: left;">
      <v-card-text style="text-align: center;">
        <v-row class="pa-0 ma-0" align="center" justify="center">
          <v-col cols="12">
            <v-img
              :src="require('@/assets/image/divider-small.png')"
              contain
              height="14"
              class="mt-4"
            >
            </v-img>
          </v-col>
        </v-row>
        <v-row class="pa-0 ma-0" align="center" justify="center">
          <v-col cols="12">
            <p class="p-subtitle p-bold">
              입력했던 비밀번호 숫자 4자리를 입력해주세요
            </p>
            <v-divider> </v-divider>
          </v-col>
        </v-row>
      </v-card-text>
      <v-card-text class="px-6 py-0">
        <v-row class="pa-0 ma-0" align="center" justify="center">
          <v-col cols="10" class="pa-1">
            <v-text-field
              v-model="password"
              type="password"
              clearable
              dense
              solo
              height="40"
              counter="4"
              :maxlength="4"
              placeholder="비밀번호 숫자 4자리를 입력해주세요."
              hint="숫자 4자리 비밀번호"
            >
            </v-text-field>
          </v-col>
          <v-col class="pa-0" cols="4">
            <v-btn block color="white" dense rounded @click="deleteGuestbook()">
              <p class="p-subtitle ma-0">삭제</p>
            </v-btn>
          </v-col>
        </v-row>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="green darken-1" text @click="hidePopup"
          >닫기</v-btn
        >
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "DeleteGuestbook",
  components: {

  },
  props: {
    showPopup: {
      type: Boolean,
      required: true,
      default: false,
    },
  },
  methods: {
    hidePopup() {
      this.$emit("hidePopup")
    },
    clear() {
      this.password = ""
      this.item = null
    },
    setItem(item) {
      this.password = ""
      this.item = item
    },
    deleteGuestbook() {
      var data = {
        item: this.item,
        password: this.password
      }
      
      this.$emit("deleteComment", data)
    },
  },
  data: () => ({
    password: "",
    item: null,
  }),
};
</script>

