<template>
  <v-dialog v-model="showPopup" style="overflow: hidden !important;" width="1124">
    <v-card class="pa-0 ma-0">
      <v-card-text class="pa-0 ma-0">
        <v-row class="pa-0 ma-0" align="center" justify="center">
          <v-col cols="12" class="pa-0" style="overflow: hidden !important;">
            <v-btn
              icon
              dense
              style="z-index: 99; top: 1%; left: 99%; opacity: .4; transform: translateX(-100%);"
              absolute
              @click="hidePopup()"
            >
              <v-icon
                color="white"
                small
              >
                mdi-window-close
              </v-icon>
            </v-btn>
            <v-btn
              icon
              dense
              :style="isVertical ? 'z-index: 99; top: 44%; left: 1%; opacity: .4;' : 'z-index: 99; top: 48%; left: 1%; opacity: .4;'"
              absolute
              :disabled="target == 0"
              @click="swipe('Right')"
            >
              <v-icon
                color="white"
                small
                dense
              >
                mdi-chevron-left
              </v-icon>
            </v-btn>
            <v-img
              :src="photoItems[target].src"
              :lazy-src="photoItems[target].src"
              class=""
              contain
              v-touch="{
                left: () => swipe('Left'),
                right: () => swipe('Right'),
              }"
            >
              <template v-slot:placeholder>
                <v-row
                  class="fill-height ma-0"
                  align="center"
                  justify="center"
                >
                  <v-progress-circular
                    indeterminate
                    color="grey lighten-5"
                  ></v-progress-circular>
                </v-row>
              </template>
            </v-img>
            <v-btn
              icon
              dense
              class="pa-0 ma-0"
              :style="isVertical ? 'z-index: 99; top: 44%; left: 99%; opacity: .4; transform: translateX(-100%);' : 'z-index: 99; top: 48%; left: 99%; opacity: .4; transform: translateX(-100%);'"
              absolute
              :disabled="(target + 1) == photoItems.length"
              @click="swipe('Left')"
            >
              <v-icon
                color="white"
                small
                dense
              >
                mdi-chevron-right
              </v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </v-card-text>
      <!-- <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="green darken-1" text @click="hidePopup"
          >닫기</v-btn
        >
      </v-card-actions> -->
    </v-card>
  </v-dialog>
</template>

<script>

export default {
  name: "PhotoViewer",
  components: {

  },
  props: {
    showPopup: {
      type: Boolean,
      required: true,
      default: false,
    },
    photoItems: {
      type: Array,
      required: false,
      default: [],
    },
    isVertical: {
      type: Boolean,
      required: false,
      default: true,
    },
  },
  methods: {
    hidePopup() {
      this.$emit("hidePopup")
    },
    clear() {
      this.target = 0
    },
    swipe (direction) {
      if (direction == "Left") {
        this.target++
      } else if (direction == "Right") {
        this.target--
      }
    },
    setPhotoData(target) {
      if (target != null) {
        this.target = target
      } else {
        this.clear()
      }
    },
  },
  data: () => ({
    target: 0,
    swipeDirection: 'None',
  }),
};
</script>

<style scoped>
  
</style>